import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/App.css';

export class CardElements extends PureComponent {

    render() {
        const light = { backgroundColor: '#ffffff' };
        const dark = { backgroundColor: '#454647' };
        return (
            <div className={ "card-elements-container" } style={ document.body.getAttribute('scheme') === 'client_light' ? light : dark }>
                {this.props.children}
            </div>
        )
    }

}